@use '../helpers/' as *;

.select {
  &__container {
    display: inline-block;
    position: relative;
    text-transform: uppercase;
  }

  &__value {
  //   display: flex;
  // align-items: center;
  // justify-content: space-between;
  // width: 200px;
  // border: var(--border);
  // padding: .25rem .5rem;
  
  cursor: pointer;
  user-select: none;

  }

  &__options {
    width: 100%;
    position: absolute;
    top: calc(100% + #{rem(6)});
    display: grid;
    justify-content: center;
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius-small);
    transition-duration: var(--transition-duration);
    // max-height: 200px;
    // overflow-y: auto; 
    transition-duration: var(--transition-duration);
    // @include hide;

    &.is-expanded {
      @include show;
    }

    
  }

  &__option {
    width: calc(100% - #{rem(1)});
    padding-block: rem(12);
    // padding: rem(12) rem(18);
    
    &:not(:last-child) {
      border-bottom: 1px solid var(--background-basic);
    }
    &:first-child {
      border-radius: var(--radius-small) var(--radius-small) 0 0;
    }
    &:last-child {
      border-radius: 0 0 var(--radius-small) var(--radius-small);
    }
    @include hover {
      background-color: var(--elements-accent-pale);
    }
    &.is-selected {
      background-color: var(--elements-accent);
      pointer-events: none;
    }
    &.is-current {
      border-color: var(--elements-accent);
    }
  }
  
  
