@use '../helpers/' as *;

.tabs {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: fluid(60, 40);

    &__header {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(10);
      
      &.button {
        background-color: var(--elements-accent-pale-more);
      }
    }
  }