@font-face {
  font-family: "Poppins Light";
  src: url("../fonts/Poppins-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Poppins Bold";
  src: url("../fonts/Poppins-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html.is-lock {
  overflow: hidden;
}

body {
  font-size: clamp(0.9375rem, 0.890625rem + 0.234375vw, 1.125rem);
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  font-family: var(--font-family-basic);
  color: var(--text-basic);
  background-color: var(--background-basic);
}

.container {
  max-width: calc(var(--container-width) + var(--container-padding-x) * 2);
  margin-inline: auto;
  padding-inline: var(--container-padding-x);
}

main {
  flex-grow: 1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--text-basic);
  font-weight: 700;
}

.h1 {
  font-size: clamp(2.25rem, 0.9375rem + 6.5625vw, 7.5rem);
  line-height: 1.1;
}

.h2 {
  font-size: clamp(1.75rem, 1.25rem + 2.5vw, 3.75rem);
  font-family: var(--font-family-titles);
  line-height: 1.1;
  letter-spacing: 0.09rem;
}

.h3 {
  font-size: clamp(1.375rem, 1.25rem + 0.625vw, 1.875rem);
}

.h4 {
  font-size: clamp(1rem, 0.875rem + 0.625vw, 1.5rem);
  font-family: var(--font-family-titles);
  text-transform: uppercase;
  letter-spacing: 0.06rem;
}

.h5 {
  font-size: clamp(0.875rem, 0.8125rem + 0.3125vw, 1.125rem);
  font-family: var(--font-family-titles);
  text-transform: uppercase;
  letter-spacing: 0.06rem;
}

.h6 {
  font-size: clamp(0.875rem, 0.84375rem + 0.15625vw, 1rem);
  text-transform: uppercase;
  letter-spacing: 0.06rem;
}

button {
  padding: 0;
  text-decoration: none;
  color: inherit;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

a {
  color: var(--text-additional);
}

a[class] {
  text-decoration: none;
}

:focus-visible {
  outline: 0.125rem solid var(--text-additional);
  outline-offset: 0.375rem;
  border-radius: 0.0625rem;
  transition-duration: 0s !important;
}

::-webkit-scrollbar {
  width: 0.25rem;
  height: 0.25rem;
}

::-webkit-scrollbar-track {
  background: var(--background-additional);
}

::-webkit-scrollbar-thumb {
  height: 1.25rem;
  background: linear-gradient(to bottom, var(--elements-accent) 40%, var(--elements-accent-hover) 60%);
  border-radius: 4px;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

.ios::first-letter {
  text-transform: lowercase;
}

.text-gradient {
  background: var(--text-gradient);
  background-size: 200%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: animate-gradient 5s linear infinite;
}

.preview-icon {
  width: clamp(2.5rem, 2.1875rem + 1.5625vw, 3.75rem);
}

.preview-icon--subtitle {
  width: clamp(1.625rem, 1.3125rem + 1.5625vw, 2.875rem);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  border: var(--border-form);
  -webkit-text-fill-color: var(--text-basic) !important;
  -webkit-box-shadow: 0 0 0px 1000px var(--background-additional) inset;
  transition: background-color 5000s ease-in-out 0s;
}

.non-animated {
  animation-play-state: paused;
}

.circle-btn {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: var(--text-basic);
  background-color: var(--elements-accent);
  transition: background 0.4 ease-in-out;
}

.circle-btn svg {
  width: 24px;
  height: 24px;
  color: inherit;
  fill: currentColor;
}

.circle-btn.swiper-button-disabled {
  opacity: 0.4;
  pointer-events: none;
}

[role=tab][aria-selected=true] {
  color: var(--text-basic);
  background-color: var(--elements-accent);
  pointer-events: none;
}

[role=tabpanel][hidden=hidden] {
  display: none;
}

:root {
  --text-basic: #fffefe;
  --text-additional: #b6bad3;
  --text-placeholder: #b6bad3be;
  --background-basic: #0f1123;
  --background-additional: #14162e;
  --background-form-field: #191d3b;
  --elements-accent: #5847eb;
  --elements-accent-pale: #5747ebb1;
  --elements-accent-pale-more: #5747eb7b;
  --elements-additional: #363c7d;
  --elements-accent-additional: #0f145d;
  --elements-accent-extra: #0891b2;
  --elements-accent-extra2: #5ed8f7;
  --border-color: #444866;
  --border-form-color: #2b3045;
  --border-hover: #606690;
  --border: 0.125rem solid var(--border-color);
  --border-form: 0.0625rem solid var(--border-form-color);
  --radius: clamp(0.375rem, 0.3125rem + 0.3125vw, 0.625rem);
  --radius-small: 0.375rem;
  --error: #c115df;
  --slide-transition: cubic-bezier(0.4, 0, 0.2, 1);
  --gradient: linear-gradient(to right, var(--elements-accent) 55%, var(--elements-accent-additional) 126%);
  --text-gradient: linear-gradient(
    to right,
    var(--elements-accent-extra),
    var(--elements-accent-pale),
    var(--elements-accent),
    var(--elements-additional),
    var(--elements-accent-additional),
    var(--elements-accent-extra)
  );
  --shadow: 0 0.25rem 0.5rem rgba(9, 11, 11, 0.5);
  --font-family-basic: "Poppins Light", sans-serif;
  --font-family-titles: "Poppins Bold", sans-serif;
  --container-width: 100rem;
  --container-padding-x: 1.25rem;
  --transition-duration: 0.3s;
  --transition-link: background-color var(--transition-duration) ease-in-out, color var 0.4s ease-in-out,
    font-weight 0.5s ease-in-out;
  --transition-with-border: background-color var(--transition-duration) ease-in-out, border 0.4s ease-in-out;
  --transition-with-color-text: box-shadow var(--transition-duration) ease-in,
    background-color var(--transition-duration) ease-in-out, color 0.5s ease-in-out;
  --slider-height: clamp(25rem, 24.375rem + 3.125vw, 27.5rem);
  --slider-height-mobile: clamp(18.75rem, 17.1875rem + 7.8125vw, 25rem);
  --slider-item-height: clamp(3.4375rem, 3.359375rem + 0.390625vw, 3.75rem);
  --slider-item-height-mobile: clamp(2.875rem, 2.734375rem + 0.703125vw, 3.4375rem);
  --100vw: calc(100vw - var(--scrollbar-width));
}

@keyframes line-animation {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes spin {
  from {
    --angle: 0deg;
  }

  to {
    --angle: 360deg;
  }
}

@keyframes boxShadow {
  to {
    box-shadow: var(--shadow);
  }
}

@keyframes rotating-icon {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes flicker {
  to {
    mask-position: 50% 50%, 0 50%;
  }
}

@keyframes bounce {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.15);
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes animate-gradient {
  to {
    background-position: 200%;
  }
}

@keyframes image-left {
  0% {
    transform: rotateY(-3deg) scaleX(0.9);
  }

  100% {
    transform: rotateY(0deg) scaleX(1);
  }
}

@keyframes image-right {
  0% {
    transform: rotateY(0deg) scaleX(1);
  }

  100% {
    transform: rotateY(3deg) scaleX(0.9);
  }
}

