}

.microsoft__nav-btn {
  transition: transform 0.5s ease-in-out;
}

.microsoft__screens-wrapper {
  width: 100%;
  display: block;
}

.microsoft__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  transition: opacity var(--transition-duration) ease-in-out;
}

.microsoft__item .microsoft__image {
  width: 100%;
  padding-top: clamp(1.25rem, 0.9375rem + 1.5625vw, 2.5rem);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  order: 2;
}

.microsoft__item .microsoft__image::after {
  content: "";
  width: 100%;
  height: 0.125rem;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(90deg, rgba(15, 17, 35, 0) 0%, var(--elements-accent-pale) 50%, rgba(15, 17, 35, 0) 100%);
}

.microsoft__item .microsoft__image img {
