}

.scrollup {
  margin-left: auto;
  margin-bottom: 1.625rem;
  margin-right: 1.625rem;
  position: sticky;
  bottom: 1.875rem;
  box-shadow: var(--shadow);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: all var(--transition-duration) ease-in-out;
}

.scrollup svg {
  rotate: -90deg;
}

.scrollup.show-scroll {
  opacity: 1;
  visibility: visible;
  animation: bounce 1.5s ease-in-out;
  animation-iteration-count: 2;
}

.section {
  padding-top: clamp(6.25rem, 5.3125rem + 4.6875vw, 10rem);
}

.section__title {
  margin-bottom: clamp(2.5rem, 2.1875rem + 1.5625vw, 3.75rem);
