}

.select__container {
  display: inline-block;
  position: relative;
  text-transform: uppercase;
}

.select__value {
  cursor: pointer;
  user-select: none;
}

.select__options {
  width: 100%;
  position: absolute;
  top: calc(100% + 0.375rem);
  display: grid;
  justify-content: center;
  background-color: var(--background-additional);
  border: var(--border);
  border-radius: var(--radius-small);
  transition-duration: var(--transition-duration);
  transition-duration: var(--transition-duration);
}

.select__options.is-expanded {
  opacity: 1;
  visibility: visible;
}

.select__option {
  width: calc(100% - 0.0625rem);
  padding-block: 0.75rem;
}

.select__option:not(:last-child) {
  border-bottom: 1px solid var(--background-basic);
}

.select__option:first-child {
  border-radius: var(--radius-small) var(--radius-small) 0 0;
}

.select__option:last-child {
  border-radius: 0 0 var(--radius-small) var(--radius-small);
}

.select__option.is-selected {
  background-color: var(--elements-accent);
  pointer-events: none;
}

.select__option.is-current {
  border-color: var(--elements-accent);
}

@property --angle {
  syntax: "<angle>";
  initial-value: 0deg;
  inherits: false;
}

:root {
  --line-height: 1.5em;
  --lines: 6;
  /* сколько строк показывать в свернутом виде */
  --collapsed: calc(var(--line-height) * var(--lines));
  --expanded: 100vh;
  /* «максимальная» высота при раскрытии */
  --transition-time: 0.65s;
}

.services__body {
  width: 88%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-self: start;
  row-gap: clamp(2.5rem, 2.1875rem + 1.5625vw, 3.75rem);
}

.services__top {
