  row-gap: clamp(1.25rem, 0.9375rem + 1.5625vw, 2.5rem);
}

.services__article {
  display: flex;
  flex-direction: column;
  align-self: start;
  padding: clamp(1.25rem, 1.09375rem + 0.78125vw, 1.875rem);
  row-gap: clamp(1.25rem, 0.9375rem + 1.5625vw, 2.5rem);
