  align-items: center;
  column-gap: clamp(1rem, 0.9375rem + 0.3125vw, 1.25rem);
  position: relative;
  z-index: 1;
}

.services__article-top::after {
  width: 100%;
  height: 0.125rem;
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  background-image: linear-gradient(90deg, rgba(15, 17, 35, 0) 10%, var(--elements-accent-pale) 50%, rgba(15, 17, 35, 0) 100%);
}

.services__article-icon {
  max-width: clamp(3.75rem, 3.59375rem + 0.78125vw, 4.375rem);
  max-height: clamp(3.75rem, 3.59375rem + 0.78125vw, 4.375rem);
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: var(--background-additional);
  border-radius: 50%;
}

.services__article-icon::after {
  content: "";
  position: absolute;
  inset: -0.12rem;
  z-index: -1;
  background-image: conic-gradient(from var(--angle), transparent 25%, var(--elements-accent), var(--elements-accent-additional));
  border-radius: 50%;
}

.services__article-image {
  position: relative;
  fill: var(--text-basic);
  z-index: 2;
}

.services__article-text {
  position: relative;
  max-height: var(--collapsed);
  overflow: hidden;
  line-height: var(--line-height);
  color: var(--text-additional);
  transition: color 0.6s ease-in-out, max-height 0.8s var(--slide-transition);
  cursor: pointer;
}

.services__article-text::after {
  content: ". . .";
  position: absolute;
  bottom: 0;
  right: 0;
  height: var(--line-height);
  display: inline-block;
  opacity: 1;
  transition: opacity 0.3s ease;
  pointer-events: none;
  color: var(--text-additional);
}

.services__article-text p {
  margin: 0;
}

.services__article-button {
