    overflow: hidden;
    line-height: 1.5;
    transition: max-height var(--transition-time) var(--slide-transition);
    position: relative;
    color: var(--text-basic);
  }

  .services__article-text.clamped {
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }

  .services__article-text::after {
    content: "";
  }

  .services__article-button {
    display: flex;
    align-self: end;
    position: relative;
  }

  .services__article-button::before,
  .services__article-button::after {
    position: absolute;
    top: 50%;
    translate: 0 -50%;
    content: "";
    width: clamp(1rem, 0.9375rem + 0.3125vw, 1.25rem);
    height: 0.125rem;
    background-color: var(--text-basic);
    border-radius: 0.125rem;
    transition: rotate var(--transition-duration) ease-in-out;
  }

  .services__article-button::after {
    rotate: 90deg;
  }
}

@media (width > 30.06125rem) {
  .visible-mobile-s {
    display: none !important;
  }
}

@media (width > 47.99875rem) {
  .visible-mobile {
    display: none !important;
  }
}

@media (width > 63.99875rem) {
  .visible-laptop {
    display: none !important;
  }
}

@media (width <= 90.06125rem) {
  :root {
    --container-width: 80rem;
  }
}

@media (width <= 33.6875rem) {
  .contact__article {
    grid-template-columns: 1fr;
    row-gap: clamp(1.25rem, 0.9375rem + 1.5625vw, 2.5rem);
  }

  .contact__article-image-wrapper {
    margin: 0;
  }

  .footer__body {
    grid-template-columns: repeat(1, 1fr);
  }

  .footer__column-title {
    text-align: center;
  }
}

@media (orientation: landscape) and (max-height: 500px) {
  .hero__container {
    row-gap: 1.25rem;
  }

  .hero__container .hero__description {
    font-size: 1.25rem;
  }

  .hero__title-stroked {
    width: 25rem;
    height: 6rem;
  }

  .section--hero {
    padding-top: 3.75rem;
  }
}

