@use '../helpers/' as *;

.ios {
    &__body {
    width: 88%;
    margin: 0 auto;
    
    @include laptop {
      width: 100%;
    }
  }
  &__article {
    margin-bottom: fluid(40, 20);
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

  &__screens-wrapper {
    padding-inline: fluid(30, 10);
    padding-block: fluid(30, 20);
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
    user-select: none;
    transition: var(--transition-with-border);
    overflow: hidden;

    @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-basic);
    }
  }

  &__item {
  // height: 440px;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: var(--main-shadow);
  border-radius: 4px 4px 0 0;
  // transition: opacity var(--transition-duration) ease-in-out;
  cursor: pointer;

  @include hover {
    .ios__item-content {
      bottom: 0;
      backdrop-filter: blur(rem(20));
      // opacity: 1;
    }
  }

  &-content {
    width:80%;
    display: flex;
    flex-direction: column;
    left: 0;
    bottom: -60%;
    right: 0;
    position: absolute;
    margin-inline: rem(13);
    padding-block: fluid(30, 20);
    // padding-inline: fluid(16, 10);
    padding-inline: rem(6);
    background-color: transparent;
    transition: bottom 0.5s ease-in-out;
    z-index: 1;
  }

  &-title {
    margin-bottom: fluid(20, 10);
    padding-bottom: rem(20);
    position: relative;
    line-height: 150%;

    &::after {
        width: 100%;
        height: rem(2);
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
  }
}

  &__image {
    height: 100%;
    width: auto;
  }

  &__description {
    @include fluid-text(16, 14);
  }

}


  
