@use '../helpers/' as *;

.field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: rem(10);

  @include mobile {
    row-gap: rem(10);
  }

  &__control {
    width: 100%;
    padding: rem(12.42) rem(12);
    background-color: var(--background-form-field);
    border: var(--border-form);
    border-radius: var(--radius-small);
    color: var(--text-basic);
    transition: var(--transition-with-border);

    &::placeholder {
      color: var(--text-placeholder);
      transition: color 0.2s ease-out;
    }

    &:user-invalid {
      border-color: var(--error);
    }

    &:user-valid {
      border-color: var(--border-hover);
      background-color: var(--background-additional);
    }

    @include laptop {
      padding-inline: rem(10);
    }

    @include hover {
      background-color: var(--background-additional);
      border-color: var(--border-hover);
    }

    &:active,
    &:focus {
      border-color: var(--border-hover);
      background-color: var(--background-additional);
    }

    &:focus {
      &::placeholder {
        color: transparent;
      }
    }
  }

  &__required-star {
    color: var(--elements-accent);
  }

  &__hint {
    color: var(--error);
    @include fluid-text(16, 14);
    opacity: 0;
    transition: opacity var(--transition-duration) ease-in-out;
  }
}

textarea.field__control {
  resize: vertical;
  min-height: rem(144);
  padding-block: 1rem;

  @include mobile {
    min-height: rem(160);
  }
}
