@use '../helpers/' as *;

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

.header {
  width: 100%;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: none;
  transition: background-color 0.8s cubic-bezier(0.64, 0.04, 0.26, 0.87), box-shadow 0.8s ease-in-out;

  z-index: 3;

  &.is-active {
    background-color: var(--background-basic);
    box-shadow: var(--shadow);
  }

  &__body {
    padding-block: rem(20);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mobile {
      padding-block: rem(16);
    }
  }

  &__content {
    display: flex;
    column-gap: 1.5rem;
    align-items: center;
  }

  &__select {
    text-transform: uppercase;
  }

  &__menu {
    @include laptop {
      width: 100%;
      height: 100svh;
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: var(--background-additional);
      transform: translate3d(-100%, 0, 0);
      transition: transform 0.4s cubic-bezier(0.64, 0.04, 0.26, 0.87);

      &.is-active {
        transform: translate3d(0, 0, 0);
      }
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: 1rem;

      @include laptop {
        flex-direction: column;
        flex-wrap: nowrap;
        row-gap: 1rem;
        padding-block: rem(34);
        padding-inline: rem(70);
        overflow-y: auto;

        // ::-webkit-scrollbar {
        //   width: rem(2);
        //   // height: rem(6);
        // }

        // ::-webkit-scrollbar-track {
        //   background: var(--background-additional);
        // }

        // ::-webkit-scrollbar-thumb {
        //   height: rem(20);
        //   background: linear-gradient(to bottom, var(--elements-accent) 40%, var(--elements-accent-hover) 60%);
        //   border-radius: 4px;
        // }
      }
    }

    &-link {
      position: relative;
      // padding: rem(9.42) rem(12);
      padding: rem(12) rem(12);
      text-align: center;
      border-radius: var(--radius-small);
      transition: var(--transition-link);
      cursor: pointer;

      &::after {
        content: '';
        position: absolute;
        inset: -0.12rem;
        z-index: -1;
        background-image: conic-gradient(
          from var(--angle),
          transparent 25%,
          var(--elements-accent),
          var(--elements-accent-additional)
        );
        border-radius: calc(var(--radius-small) + rem(2));
        visibility: hidden;

        pointer-events: none;
      }

      @include hover {
        color: inherit;
        font-weight: 700;
        background-color: var(--background-basic);
        &::after {
          visibility: visible;
          animation: 0.8s spin linear 1;
        }
      }

      &.is-link-active {
        color: inherit;
        font-weight: 700;
        pointer-events: none;
        background-color: var(--background-basic);

        &::after {
          visibility: visible;
        }
      }
    }
  }

  &__burger-button {
    z-index: 2;
  }
}
