@use '../helpers/' as *;

.hero {
  height: 100svh;
  position: relative;
  display: flex;
  align-items: center;

  &__video {
    height: 100svh;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.6;
    visibility: hidden;
  }

  &__background {
    width: 0%;
    height: 100svh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--background-basic);
    transition: width 0.5s var(--slide-transition);
  }

  &__container {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    row-gap: rem(60);
    z-index: 2;

    @media (orientation: landscape) and (max-height: 500px) {
      row-gap: rem(20);
      .hero__description {
        font-size: rem(20);
      }
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    // margin-bottom: fluid(100, 60);
    // margin-bottom: rem(100);
    // @include laptop {
    //   margin-bottom: rem(60);
    // }
    // @include mobile {
    //   margin-bottom: rem(60);
    // }
  }

  &__title-name {
    line-height: 0;
  }

  &__title-stroked {
    width: fluid(780, 268);
    height: fluid(250, 87);

    @media (orientation: landscape) and (max-height: 500px) {
      width: rem(400);
      height: rem(96);
    }
  }

  &__subtitle {
    text-transform: uppercase;
    color: var(--elements-accent);

    &.non-animated {
      animation-play-state: paused;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    row-gap: rem(6);
  }

  &__animation-space {
    --size: 20px;
    background-color: var(--background-basic);
    width: 100%;
    height: 100svh;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 1;
    z-index: 1;

    background: conic-gradient(
      from 180deg at 50% 70%,
      var(--text-basic) 0deg,
      var(--background-additional) 72.0000010728836deg,
      var(--elements-accent-additional) 144.0000021457672deg,
      var(--elements-accent) 144.0000021457672deg,
      var(--elements-accent-extra) 216.00000858306885deg,
      var(--elements-accent-extra2) 288.0000042915344deg,
      var(--text-basic) 1turn
    );
    mask: radial-gradient(circle at 50% 50%, white 2px, transparent 2.5px) 50% 50% / var(--size) var(--size),
      url('../img/noise-mask.webp') 256px 50% / 256px 256px;
    mask-composite: intersect;
    animation: flicker 20s infinite linear;
    animation-play-state: running;

    &.non-animated {
      animation-play-state: paused;
    }
  }
}


