@keyframes bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.15);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes animate-gradient {
  to {
    background-position: 200%;
  }
}

@keyframes image-left {
  0% {
    transform: rotateY(-3deg) scaleX(0.90);
  }

  100% {
    transform: rotateY(0deg) scaleX(1);
  }
}

@keyframes image-right {
  0% {
    transform: rotateY(0deg) scaleX(1);
  }

  100% {
    transform: rotateY(3deg) scaleX(0.90);
  }
}

