@use '../helpers/' as *;

.microsoft {
  &__body {
    width: 88%;
    margin: 0 auto;
    
    @include laptop {
      width: 100%;
    }
  }
    
  &__questions {
    margin-bottom: fluid(100, 60);
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: rem(100);
    
    @include laptop {
      column-gap: fluid(60, 40);
    }

    @include mobile {
      grid-template-columns: 1fr;
      row-gap: fluid(60, 40);
    }
  }

  &__article {
    display: grid;
    grid-template-columns: 1fr;
    align-self: start;
    row-gap: fluid(40, 20);
  }

  &__preview-description {
    margin-bottom: 0;
  }

  &__preview-button {
    margin-right: auto;
  }

  &__accordion-list {
    display: grid;
    row-gap: fluid(40, 30);

    @include mobile {
      row-gap: fluid(20, 16);
    }

    &--has-counter {
      --accordionListCounterHeight: #{fluid(52, 44)};
      counter-reset: custom-counter;
    
      .microsoft__accordion-item {
        display: flex;
        column-gap: fluid(20, 16);
        counter-increment: custom-counter;
        &::before {
          @include flex-center;
          content: counter(custom-counter, decimal-leading-zero);
          min-width: #{fluid(54, 46)};
          height: var(--accordionListCounterHeight);
          padding-top: rem(3);
          background-color: var(--background-additional);
          @include fluid-text(20, 18);
          font-weight: 700;
          border-radius: var(--radius-small);
          border: var(--border);
        }
      }
    }
  }
  &__accordion-item {
    padding-block: 1rem;
    &:not(:last-child) {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;

        height: rem(2);
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 3fr;
    column-gap: rem(100);
    padding-inline: fluid(30, 10);
    padding-block: fluid(30, 20);
    background-color: var(--background-additional);
    border: var(--border);
    border-radius: var(--radius);
    user-select: none;
    transition: var(--transition-with-border);

    @include hover {
      border-color: var(--border-hover);
      background-color: var(--background-basic);
    }

    @include laptop {
      grid-template-columns: 1fr 2fr;
      column-gap: fluid(60, 40);
    }

    // @include mobile {
    //   grid-template-columns: 1fr;
    //   row-gap: fluid(60, 40);
    // }

    @media (max-width: 767.98px) { // вместо @include mobile
      grid-template-columns: 1fr;  // одна колонка
      gap: 1.8rem;
    }
  }

//   microsoft__content {
//   display: grid;
//   grid-template-columns: 1fr 3fr;
//   gap: 2rem;

//   @media (max-width: 768px) { // вместо @include mobile
//     grid-template-columns: 1fr;  // одна колонка
//     gap: 1.5rem;
//   }
// }

// .microsoft__image img {
//   max-width: 100%; // ломаю фиксированную ширину
//   height: auto;
//   display: block;
// }

  &__nav {
    padding-top: rem(4);
    display: flex;
    flex-direction: column;
    row-gap: fluid(40, 20);
    position: relative;

    @include mobile {
      flex-direction: row;
      flex-wrap: wrap;
      gap: rem(20);
      padding-top: 0;
    }

    &-item {
      display: flex;
      align-items: center;
      cursor: pointer;
      transition: transform var(--transition-duration);

      &--active {
        pointer-events: none;
        .microsoft__nav-dot {
          background-color: var(--elements-accent);
          transform: scale(1.5);
        }
        .microsoft__nav-btn {
          color: var(--text);
        }
      }
      
      @include hover {
        .microsoft__nav-btn {
          color: var(--text);
          transform: translateX(1rem) scale(1.2);
          transform-origin: 'left center';
        }

        .microsoft__nav-dot {
          background-color: var(--elements-accent);
          transform: scale(1.5);
        }
      }
    }
    &-dot {
      width: rem(12);
      height: rem(12);
      border-radius: 50%;
      background-color: var(--border-color);
      margin-right: fluid(20, 10);
      transition: background-color var(--transition-duration) ease-in-out, transform 0.5s ease-in-out;
    }

    &-btn {
      transition: transform 0.5s ease-in-out;
    }
  }

  &__screens-wrapper {
    width: 100%;
    display: block; // для Swiper
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    transition: opacity var(--transition-duration) ease-in-out;

    .microsoft__image {
      width: 100%;
      padding-top: fluid(40, 20);
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      order: 2;

      &::after {
        content: '';
        width: 100%;
        height: rem(2);
        position: absolute;
        top: 0;
        left: 0;
        background-image: linear-gradient(
          90deg,
          rgba(15, 17, 35, 0) 0%,
          var(--elements-accent-pale) 50%,
          rgba(15, 17, 35, 0) 100%
        );
      }

      img {
        width: 100%;
        max-width: rem(580);
        height: auto;
        // max-width: 100%; // ломаю фиксированную ширину
        // height: auto;
        border-radius: var(--radius);
      }
    }

    .microsoft__description {
      padding-bottom: fluid(30, 20);
      order: 1;
    }
  }

  &__screens-pagination {
    margin-top: rem(20);
  }
}

