button {
  padding: 0;
  text-decoration: none;
  color: inherit;

  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

a {
  color: var(--text-additional);

  @include hover {
    color: var(--text-basic);
  }

  &[class] {
    text-decoration: none;
  }
}

:focus-visible {
  outline: rem(2) solid var(--text-additional);
  outline-offset: rem(6);
  // border-radius: var(--radius);
  border-radius: rem(1);
  transition-duration: 0s !important;
}

