::-webkit-scrollbar {
  width: rem(4);
  height: rem(4);
}

::-webkit-scrollbar-track {
  background: var(--background-additional);
}

::-webkit-scrollbar-thumb {
  height: rem(20);
  background: linear-gradient(to bottom, var(--elements-accent) 40%, var(--elements-accent-hover) 60%);
  border-radius: 4px;
}

.visually-hidden {
  @include visually-hidden;
}

.hidden-mobile {
  @include mobile {
    display: none !important;
  }
}

.hidden-mobile-s {
  @include mobile-s {
    display: none !important;
  }
}

.hidden-laptop {
  @include laptop {
    display: none !important;
  }
}

.visible-mobile-s {
  @include mobile-s-above {
    display: none !important;
  }
}

.visible-mobile {
  @include mobile-above {
    display: none !important;
  }
}

.visible-laptop {
  @include laptop-above {
    display: none !important;
  }
}

.ios::first-letter {
  text-transform: lowercase;
}

.text-gradient {
  background: var(--text-gradient);
  background-size: 200%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: animate-gradient 5s linear infinite;
}

.preview-icon {
  width: fluid(60, 40);

  &--subtitle {
    width: fluid(46, 26)
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus {
  border: var(--border-form);
  -webkit-text-fill-color: var(--text-basic) !important;
  -webkit-box-shadow: 0 0 0px 1000px var(--background-additional) inset;
  transition: background-color 5000s ease-in-out 0s;
}

.non-animated {
  animation-play-state: paused;
}

.circle-btn {
  width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: var(--text-basic);
    background-color: var(--elements-accent);
    transition: background 0.4 ease-in-out;

    @include mobile {
      width: 44px;
      height: 44px;
    }
    
    svg {
      width: 24px;
      height: 24px;
      color: inherit;
      fill: currentColor;
    }
  
    @include hover {
      background: linear-gradient(to bottom, var(--elements-accent-extra), var(--elements-accent));
      background-color: var(--elements-accent);
    }

    &.swiper-button-disabled {
      opacity: 0.4;
      pointer-events: none;
    }
}

[role=tab][aria-selected="true"] {
  color: var(--text-basic);
  background-color: var(--elements-accent);
  pointer-events: none;
}

[role=tabpanel][hidden="hidden"] {
  display: none;
}

