@use '../helpers/' as *;
.scrollup {
  margin-left: auto;
  margin-bottom: rem(26);
  margin-right: rem(26);
  position: sticky;
  bottom: rem(30);
  box-shadow: var(--shadow);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: all var(--transition-duration) ease-in-out;

  & svg {
    rotate: -90deg;
  }

  &.show-scroll {
    opacity: 1;
    visibility: visible;
    animation: bounce 1.5s ease-in-out;
    animation-iteration-count: 2;
  }
}
