@use '../helpers/' as *;

.button {
  padding: rem(14) rem(16);
  white-space: nowrap;
  color: var(--text-basic);
  background-color: var(--elements-accent);
  border-radius: var(--radius-small);
  box-shadow: none;
  transition: transform var(--transition-duration) ease-in-out, background 0.4s ease-in-out,
    font-size 0.5s ease-in-out;
  user-select: none;
  will-change: transform;

  &--select {
    padding: rem(9.42) rem(12.72);
  }

  &--tabs {
    background-color: var(--elements-accent-pale-more);
    color: var(--text-additional);

    @include hover {
      color: var(--text-basic);
    }   
  }

  @include hover {
    transform: translateY(rem(-4));
    background: linear-gradient(to bottom, var(--elements-accent-extra), var(--elements-accent));
    font-weight: 700;
    background-color: var(--elements-accent);
  }
}

