@use '../helpers/' as *;

.card {
   width: 100%;
   display: flex;
   flex-direction: column;

    &__top {
      margin-bottom: fluid(60, 40);
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__title {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      column-gap: fluid(20, 10);
      row-gap: rem(20);

      &-link {
        width: fluid(90, 80);
        height: fluid(40, 30);
        display: flex;
        justify-content: center;
        align-items: center;
        
        @include hover {
          .tabs__content-link-icon {
            opacity:0.6;
            
          }
        }
      }

      &-link-icon {
        transition: var(--transition-duration);
      }
    } 

    &__content-wrapper {
      display: flex;
      flex-direction: column;
    }

}

